.popup-remove__container {
    position: relative;
    width: 430px;
    background-color: #FFFFFF;
    box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.15);
    border-radius: 10px;
    margin: 0;
    padding-bottom: 37px;
}

@media all and (max-width: 682px) {

    .popup-remove__container {
        width: 282px;
        padding-bottom: 25px;
    }
}